rs[i]['hasVote']) {\n\t\t\t\t\t\tlet k = list.indexOf(this.users[i].voteView);\n\t\t\t\t\t\tif (k === -1) {\n\t\t\t\t\t\t\tk = list.length;\n\t\t\t\t\t\t\tlist[k] = this.users[i].voteView;\n\t\t\t\t\t\t\tcount[k] = 1;\n\t\t\t\t\t\t} else {\n\t\t\t\t\t\t\tcount[k]++;\n\t\t\t\t\t\t}\n\t\t\t\t\t}\n\t\t\t\t}\n\n\t\t\t\treturn {\n\t\t\t\t\tlabels: list,\n\t\t\t\t\tdatasets: [\n\t\t\t\t\t\t{\n\t\t\t\t\t\t\tlabel: '',\n\t\t\t\t\t\t\tbackgroundColor: this.colors,\n\t\t\t\t\t\t\tdata: count,\n\t\t\t\t\t\t}\n\t\t\t\t\t],\n\t\t\t\t};\n\t\t\t},\n\t\t},\n\n\t\tcomputed: {\n\t\t\tsortedUsers() {\n\t\t\t\treturn this.users.sort((a, b) => {\n\t\t\t\t\tif (a.isOwner && !b.isOwner) return -1;\n\t\t\t\t\tif (!a.isOwner && b.isOwner) return 1;\n\n\t\t\t\t\tif (a.hasVote && !b.hasVote) return -1;\n\t\t\t\t\tif (!a.hasVote && b.hasVote) return 1;\n\n\t\t\t\t\tif (a.name == '' && !b.name == '') return 1;\n\t\t\t\t\tif (!a.name == '' && b.name == '') return -1;\n\n\t\t\t\t\treturn a.name.localeCompare(b.name);\n\t\t\t\t});\n\t\t\t},\n\t\t},\n\n\t\twatch: {\n\t\t\taverage(value) {\n\t\t\t\tif (value !== null) {\n\t\t\t\t\tthis.chart = true;\n\t\t\t\t\tthis.$nextTick(() => {\n\t\t\t\t\t\tthis.$refs.chart.update();\n\t\t\t\t\t});\n\t\t\t\t} else {\n\t\t\t\t\tthis.chart = false;\n\t\t\t\t}\n\t\t\t},\n\t\t},\n\t}\n</script>\n\n<style scoped>\n\t.list-group-item {\n\t\tpadding: 10px;\n\t}\n\n\t.fade-enter-active {\n\t\ttransition: all .5s;\n\t}\n\t.fade-enter, .fade-leave-to {\n\t\topacity: 0;\n\t}\n</style>\n"],"sourceRoot":""}]);
