___CSS_LOADER_EXPORT___.push([module.id, "textarea.text-box[data-v-72b7d0b3]{background-color:var(--bs-card-bg);border:none;flex:1 1 auto;font-size:14px;line-height:20px;min-height:20px;min-width:0;outline:0;overflow:hidden;overflow-wrap:break-word;position:relative;resize:none;width:1%}", "",{"version":3,"sources":["webpack://./resources/js/components/TaskForm.vue"],"names":[],"mappings":"AAwLA,mCAaA,kCAAA,CAHA,WAAA,CARA,aAAA,CAOA,cAAA,CADA,gBAAA,CADA,eAAA,CAHA,WAAA,CAOA,SAAA,CANA,eAAA,CACA,wBAAA,CALA,iBAAA,CAWA,WAAA,CATA,QAWA","sourcesContent":["<template>\n\t<focusable class=\"pt-2 pl-2 pr-2 mb-4 p-2\" ref=\"focusable\" @blurred=\"onBlurred\">\n\t\t<form v-show=\"!isPreview\" @submit.prevent=\"submit\">\n\t\t\t<div class=\"input-group\">\n\t\t\t\t<textarea\n\t\t\t\t\t@keydown.enter.exact.prevent=\"submit\"\n\t\t\t\t\t@paste=\"onPasteImage\"\n\t\t\t\t\t@focus=\"$refs.focusable.focus()\"\n\t\t\t\t\tref=\"text\"\n\t\t\t\t\tclass=\"text-box\"\n\t\t\t\t\t:class=\"{'text-success': isEdit}\"\n\t\t\t\t\tv-model=\"textLocal\"\n\t\t\t\t\t:placeholder=\"placeholder\"\n\t\t\t\t\tv-autosize=\"textLocal\" rows=\"1\"\n\t\t\t\t></textarea>\n\t\t\t</div>\n\t\t</form>\n\n\t\t<vue-markdown v-if=\"isPreview\" :html=\"false\" :anchorAttributes=\"anchorAttributes\" :source=\"textLocal\"></vue-markdown>\n\n\t\t<template v-slot:footer>\n\t\t\t<button class=\"btn btn-light btn-sm\" v-show=\"!isPreview && textLocal.length !== 0\" @click.prevent=\"isPreview = true\">Preview</button>\n\t\t\t<button class=\"btn btn-light btn-sm\" v-show=\"isPreview\" @click.prevent=\"isPreview = false\">Edit</button>\n\n\t\t\t<div v-show=\"!isEdit\" class=\"btn-group\" role=\"group\">\n\t\t\t\t<input type=\"radio\" class=\"btn-check\" name=\"mode\" id=\"modeOnce\" value=\"once\" v-model=\"mode\">\n\t\t\t\t<label class=\"btn btn-light btn-sm\" for=\"modeOnce\">Once</label>\n\n\t\t\t\t<input type=\"radio\" class=\"btn-check\" name=\"mode\" id=\"modeMulti\" value=\"multi\" v-model=\"mode\">\n\t\t\t\t<label class=\"btn btn-light btn-sm\" for=\"modeMulti\">Multi</label>\n\t\t\t</div>\n\n\t\t\t<button type=\"button\" class=\"btn btn-light btn-sm\" data-bs-toggle=\"tooltip\" data-bs-placement=\"top\" data-bs-title=\"Markdown supported\">\n\t\t\t\t<i class=\"fa fa-question-circle-o\" aria-hidden=\"true\"></i>\n\t\t\t</button>\n\n\t\t\t<button\n\t\t\t\t@click.prevent=\"submit\"\n\t\t\t\ttype=\"submit\"\n\t\t\t\tclass=\"btn btn-sm float-end\"\n\t\t\t\t:class=\"{'btn-success': isEdit, 'btn-primary': !isEdit}\"\n\t\t\t\t:disabled=\"textLocal.length === 0\"\n\t\t\t>{{button}}</button>\n\t\t</template>\n\t</focusable>\n</template>\n\n<script>\n\timport VueMarkdown from 'vue-markdown';\n\timport Focusable from '@/js/components/Focusable';\n\n\timport Vue from 'vue';\n\timport VueAutosize from 'autosize-vue';\n\tVue.use(VueAutosize);\n\n\timport Prism from 'prismjs';\n\timport 'prismjs/themes/prism.css';\n\n\timport 'prismjs/components/prism-markup-templating.js';\n\timport 'prismjs/components/prism-php';\n\timport 'prismjs/components/prism-bash';\n\n\texport default {\n\t\tprops: {\n\t\t\ttext: {\n\t\t\t\ttype: String,\n\t\t\t\trequired: true,\n\t\t\t},\n\t\t\tplaceholder: {\n\t\t\t\ttype: String,\n\t\t\t\trequired: true,\n\t\t\t},\n\t\t\tbutton: {\n\t\t\t\ttype: String,\n\t\t\t\trequired: true,\n\t\t\t},\n\t\t\tisEdit: {\n\t\t\t\ttype: Boolean,\n\t\t\t\tdefault: false,\n\t\t\t},\n\t\t},\n\n\t\tcomponents: {\n\t\t\tVueMarkdown,\n\t\t\tFocusable,\n\t\t},\n\n\t\tdata: () => ({\n\t\t\ttextLocal: '',\n\t\t\tisPreview: false,\n\t\t\tmode: 'once',\n\t\t}),\n\n\t\tmethods: {\n\t\t\tonPasteImage(pasteEvent) {\n\t\t\t\tif(pasteEvent.clipboardData == false){\n\t\t\t\t    if(typeof(callback) == \"function\"){\n\t\t\t\t        console.log(undefined);\n\t\t\t\t    }\n\t\t\t\t};\n\n\t\t\t\tvar items = pasteEvent.clipboardData.items;\n\n\t\t\t\tif(items == undefined){\n\t\t\t\t    if(typeof(callback) == \"function\"){\n\t\t\t\t        console.log(undefined);\n\t\t\t\t    }\n\t\t\t\t};\n\n\t\t\t\tfor (var i = 0; i < items.length; i++) {\n\t\t\t\t    // Skip content if not image\n\t\t\t\t    if (items[i].type.indexOf(\"image\") == -1) continue;\n\t\t\t\t    // Retrieve image on clipboard as blob\n\t\t\t\t    var blob = items[i].getAsFile();\n\t\t\t\t    console.log(blob);\n\t\t\t\t}\n\t\t\t},\n\n\t\t\tsubmit() {\n\t\t\t\tif (this.mode === 'multi') {\n\t\t\t\t\tlet texts = this.textLocal.split('\\n');\n\n\t\t\t\t\tfor (var i = 0; i < texts.length; i++) {\n\t\t\t\t\t\tthis.$emit('submit', {text: texts[i]});\n\t\t\t\t\t}\n\t\t\t\t} else {\n\t\t\t\t\tthis.$emit('submit', {text: this.textLocal});\n\t\t\t\t}\n\t\t\t},\n\n\t\t\tfocus() {\n\t\t\t\tthis.$refs.text.focus();\n\t\t\t\tthis.$refs.focusable.focus();\n\t\t\t},\n\n\t\t\tonBlurred() {\n\t\t\t\tthis.isPreview = false;\n\t\t\t\tthis.$refs.text.blur();\n\t\t\t}\n\t\t},\n\n\t\tcomputed: {\n\t\t\tanchorAttributes() {\n\t\t\t\treturn {\n\t\t\t\t\ttarget: 'blank',\n\t\t\t\t\trel: 'nofollow',\n\t\t\t\t}\n\t\t\t},\n\t\t},\n\n\t\twatch: {\n\t\t\ttext() {\n\t\t\t\tthis.textLocal = this.text;\n\t\t\t},\n\n\t\t\tisPreview() {\n\t\t\t\tif (this.isPreview) {\n\t\t\t\t\tthis.$nextTick(() => {\n\t\t\t\t\t\tPrism.highlightAll();\n\t\t\t\t\t});\n\t\t\t\t}\n\t\t\t},\n\t\t},\n\t}\n</script>\n\n<style scoped>\n\ttextarea.text-box {\n\t\tposition: relative;\n\t\tflex: 1 1 auto;\n\t\twidth: 1%;\n\t\tmin-width: 0;\n\t\toverflow: hidden;\n\t\toverflow-wrap: break-word;\n\t\tmin-height: 20px;\n\t\tline-height: 20px;\n\t\tfont-size: 14px;\n\t\tborder: none;\n\t\toutline: 0;\n\t\tresize: none;\n\t\tbackground-color: var(--bs-card-bg);\n\t}\n</style>\n"],"sourceRoot":""}]);
